#surkl {
  width: 100%;
  height: calc(100vh - 55px);
  display: flex;
  background: #242323;
  margin-top: 55px;
  border-top: 1px solid #3E3E3E;
  border-bottom: 1px solid #3E3E3E;
  box-sizing: border-box; }
  #surkl #newsSources {
    width: 20%;
    height: 100%;
    border-right: 1px solid #3E3E3E; }
    #surkl #newsSources #newsSourcesHeader {
      color: #F4FBFB;
      height: 50px;
      width: 100%;
      font-family: 'Comfortaa';
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      border-bottom: 1px solid #3E3E3E;
      box-sizing: border-box; }
    #surkl #newsSources #newsSourcesFeed::-webkit-scrollbar {
      width: 5px; }
    #surkl #newsSources #newsSourcesFeed::-webkit-scrollbar-thumb {
      background: #B1B6B6;
      border-radius: 10px; }
    #surkl #newsSources #newsSourcesFeed::-webkit-scrollbar-corner {
      background: transparent; }
    #surkl #newsSources #newsSourcesFeed {
      overflow-y: scroll;
      height: calc(100% - 50px);
      width: 100%; }
    #surkl #newsSources .newsSource:hover {
