      color: #F4FBFB;
      box-sizing: border-box;
      cursor: pointer; }
      #surkl #newsSources .newsSource .newsSourceImg {
        height: 40px;
        margin-right: 10px; }
  #surkl #surkl-feed::-webkit-scrollbar {
    width: 5px;
    background: #242323; }
  #surkl #surkl-feed::-webkit-scrollbar-thumb {
    background: #545353;
    border-radius: 10px; }
  #surkl #surkl-feed::-webkit-scrollbar-corner {
    background: transparent; }
  #surkl #surkl-center {
