        #surkl #surkl-center #surkl-feed .surkl-chat-msg .surkl-chat-msgAvatar {
          width: 30px;
          height: 30px;
          border-radius: 30px;
          margin: 10px;
          border: 1px solid #c2c3c4; }
        #surkl #surkl-center #surkl-feed .surkl-chat-msg .surkl-chat-HeaderNmsg .surkl-chat-MsgUserInfo {
          width: 100%;
          height: 30px;
          display: flex;
          align-items: flex-end;
