          display: flex;
          width: 100%;
          justify-content: flex-start; }
          #surkl #surkl-center #surkl-feed .surkl-chat-msg .surkl-chat-HeaderNmsg .surkl-chat-MsgUserInfo .surkl-chat-MsgDate {
            font-size: 10px;
            margin-bottom: 2px !important;
