          box-sizing: border-box; }
    #surkl #surkl-center #surkl-chat-input::placeholder {
      font-size: 15px; }
    #surkl #surkl-center #surkl-chat-input {
      width: 100%;
      height: 70px;
      outline: none;
      resize: none;
      background: transparent;
      border: none;
      border-top: 1px solid #3E3E3E;
      padding: 10px;
      box-sizing: border-box;
      font-family: 'Comfortaa';
      color: #C2C3C4;
      font-size: 13px; }
  #surkl #surkl-members {
    width: 20%;
