#account-settings {
  width: 100%;
  height: 100%; }
  #account-settings #tabs {
    height: 30px;
    display: flex; }
    #account-settings #tabs .tab {
      width: 120px;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
      height: 30px;
      margin-left: 5px;
      display: flex;
      background: #242323;
      justify-content: center;
      align-items: center;
      color: #F4FBFB;
      font-family: 'Comfortaa';
      cursor: pointer; }
    #account-settings #tabs #account-tab {
      border-bottom: 1px solid #242323; }
  #account-settings #tabs > div:first-child {
    margin-left: 0px; }
  #account-settings .tab-win {
    height: calc(100% - 30px);
    margin-top: 1px;
    border-radius: 5px;
    border-top-left-radius: 0px; }
    #account-settings .tab-win #tab-banner-n-avatar {
      max-width: 400px;
      height: 200px;
      box-sizing: border-box;
      position: relative; }
      #account-settings .tab-win #tab-banner-n-avatar #tab-banner {
        width: 100%;
        height: 200px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        margin-left: 5px; }
      #account-settings .tab-win #tab-banner-n-avatar #tab-avatar {
        width: 100px;
        height: 100px;
        border-radius: 50px;
        position: absolute;
        bottom: -45px;
        left: 30px;
        border: 2px solid #C7A035; }
    #account-settings .tab-win .account-tab-form {
      display: flex;
      flex-direction: column;
      padding: 20px;
      margin-top: 45px;
      max-width: 400px;
      box-sizing: border-box; }
      #account-settings .tab-win .account-tab-form label {
        color: #F4FBFB;
        font-family: 'Comfortaa';
        font-size: 12;
        margin-bottom: 3px;
        margin-top: 10px; }
      #account-settings .tab-win .account-tab-form .tab-input {
        color: #F4FBFB;
        background: #3F3E3E;
        width: 100%;
        border: none;
        border-radius: 3px;
        height: 28px;
        font-family: 'Comfortaa';
        font-size: 14px;
        padding-left: 10px;
        outline: none; }
